<template lang="pug">
  //- .supplierApply 123
  -
    let formList = [
    { label:'供应商名称',name:'name'},
    { label:'统一社会信用代码',name:'creditCode',maxLength:'18'},
    { label:'法定代表人',name:'legalPerson'},
    { label:'身份证号',name:'identity',maxLength:'18',idCard:true},
    { label:'用户名',name:'username',maxLength:'12'},
    { label:'密码',name:'password',type:'password'},
    { label:'确认密码',name:'secondPassword',type:'password'},
    { label:'业务联系人',name:'businessContact'},
    { label:'业务联系人电话',name:'businessContactPhone',maxLength:'11'},
    { label:'业务负责人',name:'businessResponsible'},
    { label:'业务负责人电话',name:'businessResponsiblePhone',maxLength:'11'},
    { label:'应急联系人',name:'emergencyContact'},
    { label:'应急联系人电话',name:'emergencyContactPhone',maxLength:'11'},
    { label:'验证码',name:'codeImg',maxLength:"4"},
    ]
  -
    let formUploadList = [
    { label: '营业执照扫描件', name:"businessImg" },
    { label: '诚信经营承诺书', name:"commitmentPage" ,link:"/static/downloads/cx.doc"},
    { label: '法人授权书', name:"authorizePage" ,link:"/static/downloads/fd.doc"},
    { label: '业务负责人任命书', name:"appointPage" ,link:"/static/downloads/zr.doc"},
    { label: '设备或系统简介', name:"systemDesc" },
    ]

  layout( :showFooter="true" :showSidebar="false" )
    .supplierApply
      h1 供应商注册
      .form-wrap
        el-form( label-position="right" label-width="160px" :model="form" ref="form")
          el-form-item( label="企业所属" verify prop="companyArea")
            el-select( placeholder="请选择企业所属" style="width:100%" v-model="form.companyArea")
              el-option(label="省内" value="1")
              el-option(label="省外" value="0")

          el-form-item( label="对接类型" verify prop="contactType")
            el-select( placeholder="请选择对接类型" style="width:100%" v-model="form.contactType")
              //- el-option(label="设备对接" value="1")
              el-option(label="系统对接" value="2")
              el-option(label="软硬件适配（硬件适配人脸识别app）" value="3")

          each formItem in formList
            if formItem.label == '确认密码'
              el-form-item(label=formItem.label prop=formItem.name :verify="verifyPassword" :watch="form.password")
                 el-input( placeholder=`请输入${formItem.label}` v-model=`form.${formItem.name}` )&attributes(formItem)
            else
              el-form-item(label=formItem.label verify prop=formItem.name)
                el-input( placeholder=`请输入${formItem.label}` v-model=`form.${formItem.name}` )&attributes(formItem)
                  if formItem.label == '验证码'
                    template(slot="append")
                      img.codeImg(:src="codeImg" @click="changeImage")

          each uploadItem in formUploadList
            el-form-item(label=uploadItem.label verify prop=uploadItem.name empty-message=`请上传${uploadItem.label}` )
              if uploadItem.link
                el-input( style="display:none" placeholder=`请输入${uploadItem.label}` v-model=`form.${uploadItem.name}` )
                su-upload(v-model=`form.${uploadItem.name}`, tpl-name=uploadItem.label tpl-link=uploadItem.link)
              else
                el-input( style="display:none" placeholder=`请输入${uploadItem.label}` v-model=`form.${uploadItem.name}` )
                su-upload(v-model=`form.${uploadItem.name}`)

          el-form-item(style="margin-top:-15px,color:#f39801;")
            .upload-tip(style="color:#f39801;") 上传只支持jpg格式

          el-form-item
            <el-button class="su-button" @click="$router.push('./login')">返回</el-button>
            <el-button class="su-button" @click="handleSubmit" type="primary">提交</el-button>
</template>

<script>
// import layout from '@/layout/layout'
import suUpload from '@/components/su-upload'

export default {
  components: { suUpload },
  data () {
    return {
      form: {},
      upload: {},
      codeImg: ''
    }
  },
  beforeMount () {
    this.changeImage()
  },
  methods: {
    verifyPassword (rule, val, callback) {
      console.log(val)
      if (val !== this.form.password) callback(Error('两次输入密码不一致!'))
      else callback()
    },
    handleSubmit () {
      this.$refs.form.validate((valid) => {
        let _this = this
        // _this.$router.push('/applyResult')
        if (!valid) {
          return
        }
        // todo img check
        this.$ajax.post('/open/register', this.form)
          .then((res) => {
            this.$message.success('提交申请成功')
            setTimeout(() => {
              _this.$router.push('/applyResult')
            }, 2000)
          })
          .catch((e) => {
            this.$message.error(e.msg || '提交错误')
          })
      })
    },
    changeImage () {
      this.$ajax.get('/open/createCodeImg', { responseType: 'blob' })
        .then((res) => {
          let reader = new FileReader()
          reader.onload = (e) => {
            this.codeImg = e.target.result
          }
          reader.onerror = (e) => {
            throw e
          }
          reader.readAsDataURL(res)
          // this.codeImg = res
        })
        .catch((e) => {
          this.$message.error('获取图形验证码失败!')
        })
    }
  }
}
</script>
<style lang="scss">
.supplierApply {
  width: 100%;
  min-width: 1024px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333;
  h1 {
    display: block;
    width: 80%;
    text-align: center;
    padding: 0 0 20px;
    border-bottom: 2px solid #d8d8d8;
  }
  .form-wrap {
    width: 600px;
    margin-left: -140px;
  }
  .codeImg {
    cursor: pointer;
  }
  .su-button.el-button {
    width: 200px;
  }
}
</style>
